.image-card {
  /* box-shadow: 0 2rem 5rem rgba(160, 159, 159, 0.1); */
  transition: all 0.4s ease-out;
  border-radius: 10px;

}

.image-card:hover {
  transform: scale(1.13);

  opacity: 100%;
  filter: grayscale(0%);
  filter: blur(0px);
  filter: drop-shadow(8px 8px 10px rgb(0, 0, 0));


}