.image-list {

  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  /*   grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(7, 5vw); */
  grid-gap: 10px;
}

.image-list img {
  width: 95%;
  height: 95%;
  object-fit: cover;
  display: block;
  /* opacity: 60%; */
  filter: grayscale(70%);
  filter: blur(0.5px);


}